*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.count-particles {
  background: #000022;
  position: absolute;
  top: 48px;
  left: 0;
  width: 80px;
  color: #13e8e9;
  font-size: 0.8em;
  text-align: left;
  text-indent: 4px;
  line-height: 14px;
  padding-bottom: 2px;
  font-family: Helvetica, Arial, sans-serif;
  font-weight: bold;
  visibility: hidden;
}
.js-count-particles {
  font-size: 1.1em;
}

#stats,
.count-particles {
  margin-top: 5px;
  margin-left: 5px;
}

#stats {
  border-radius: 3px 3px 0 0;
  overflow: hidden;
}

.count-particles {
  border-radius: 0 0 3px 3px;
}

/* ---- particles.js container ---- */

#particles-js {
  position: absolute;
  width: 100%;
  height: 100%;
  background-position: 50% 50%;
  background-repeat: no-repeat;
}

.second-wrapper {
  height: 100vh;
  width: 100vw;
  position: fixed;
  background-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1001;
  background-image: radial-gradient(
    circle 248px at center,
    #16d9e3 0%,
    #30c7ec 47%,
    #46aef7 100%
  );
}

.circle-loader {
  position: relative;
  width: 20rem;
  height: 20rem;
  display: flex;
  z-index: 1002;
  justify-content: center;
  align-items: center;
}

.circle {
  border-radius: 50% 50% 50% 50%;
  position: absolute;
  border-top: 2px solid #fff;
  border-bottom: 2px solid transparent;
  border-left: 2px solid #fff;
  border-right: 2px solid transparent;
  animation: animate 2s infinite;
}

.circle_one {
  left: 45%;
  top: 45%;
  width: 5rem;
  height: 5rem;
}

.circle_two {
  left: 40%;
  top: 40%;
  width: 7rem;
  height: 7rem;
  animation-delay: 0.2s;
}

.circle_three {
  left: 35%;
  top: 35%;
  width: 9rem;
  height: 9rem;
  animation-delay: 0.4s;
}

.circle_four {
  left: 30%;
  top: 30%;
  width: 11rem;
  height: 11rem;
  animation-delay: 0.6s;
}

@keyframes animate {
  50% {
    transform: rotate(360deg) scale(0.8);
  }
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
  font-size: 62.5%;
}

@keyframes moveDown {
  0% {
    opacity: 0;
    transform: translatey(-10rem);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}

@keyframes moveUp {
  0% {
    opacity: 0;
    transform: translateY(5rem);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}

@keyframes appear {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes blink {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}

.btn--blue {
  margin-top: 2rem;
  list-style: none;
  text-decoration: none;
  color: white;
  font-weight: 400;
  text-transform: uppercase;
  padding: 1rem 3rem;
  cursor: pointer;
  border-radius: 2rem;
  font-size: 1.5rem;
  backface-visibility: hidden;
  transition: all 0.3 ease-in;
  animation: moveUp 1s ease-in;
  animation-delay: 0.5s;
  position: relative;
  z-index: 100;
  animation-fill-mode: backwards;
}
.btn--blue:hover {
  transform: translateY(-5%);
  color: white;
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.5);
}
.btn--blue:hover::after {
  opacity: 0;
  transition: 0.4s;
}

.arrow {
  margin-top: 1rem;
  backface-visibility: hidden;
  transition: all 0.3 ease-in;
  animation: blink 1s linear infinite;
  animation-delay: 1.5s;
  text-transform: uppercase;
  color: white;
  animation-fill-mode: backwards;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Raleway", Arial, sans-serif;
  line-height: 1.5;
  font-size: 1rem;
}

.title {
  font-size: 3rem;
  font-weight: 200;
  letter-spacing: 1rem;
  animation-name: moveDown;
  animation-duration: 1s;
  text-transform: uppercase;
  text-align: center;
}
.title--small {
  font-size: 1.5rem;
  text-align: center;
}

.container {
  height: 100vh;
  width: 100vw;
  position: relative;
  background-color: #171933;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-size: cover;
  background-repeat: no-repeat;
  animation-direction: alternate;
  color: white;
}
.container .title-wrapper {
  animation-name: appear;
  transition-duration: 1s;
  background-color: none;
  padding: 3rem 1rem 3rem 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

@media screen and (min-width: 576px) {
  html {
    font-size: 65%;
  }
}

@media screen and (min-width: 768px) {
  html {
    font-size: 80%;
  }
  .container .title-wrapper {
    padding: 3rem;
    background-color: rgba(0, 0, 0, 0.2);
  }
}

@media screen and (min-width: 1024px) {
  html {
    font-size: 100%;
  }
}

@media screen and (min-width: 1200px) {
  html {
    font-size: 120%;
  }
  .title {
    font-weight: 300;
  }
}

@media screen and (min-width: 1200px) and (min-width: 1400px) {
  html {
    font-size: 140%;
  }
  .title {
    font-weight: 400;
  }
}
